<template>
  <div id="#app">
    <h1 class="title vivify shake">Type The Alphabet</h1>
    <h2 class="subtitle">ËæìÂÖ•Â≠óÊØçÔºåÁúãÁúã‰Ω†ÁöÑÈÄüÂ∫¶ÊúâÂ§öÂø´ :)</h2>
    <div class="github">
      <a href="http://zoutianwei.github.io"
        ><img src="./assets/github.png" alt="github"
      /></a>
    </div>
    <div class="status">
      <p class="msg" :class="{ 'vivify spin duration-1000': upperCaseCurr === 'ÂÆåÊàê' }">{{ upperCaseCurr }}</p>
    </div>
    <div class="game">
      <input
        type="text"
        class="game-input"
        v-model="inputVal"
        placeholder="Âú®ËøôÈáåËæìÂÖ•"
        @keydown="handleKeyup"
        ref="refInput"
        autocomplete="off"
        autocapitalize="off"
        spellcheck="false"
      />
      <div
        class="game-reset"
        :class="{ 'vivify spin duration-1000': rotate }"
        @click="
          handleReset();
          handleRotate();
        "
      >
        Reset
      </div>
    </div>
    <div
      class="timer"
      :class="{ 'shake-little shake-constant': records.length }"
    >
      Êó∂Èó¥: <span class="">{{ showTime ? showTime : "0.000" }}</span
      >s
    </div>
    <transition
      enter-active-class="vivify driveInTop duration-500"
      leave-active-class="vivify driveOutTop duration-500"
    >
      <div class="result" v-if="records.length">
      <p>ÊàêÁª© üèÅ</p>
      <ul>
        <li v-for="(v, i) in records" :key="`records-${i}`">
          <span>{{ v.display }}: </span> <span>{{ v.currTime }}s</span>
        </li>
      </ul>
      </div>
    </transition>
  </div>
</template>

<script>
import _ from 'lodash';
export default {
  name: "App",
  data() {
    return {
      inputVal: "",
      curr: "a", // ÂΩìÂâçÁöÑÂ≠óÊØç
      alpha: this.createAlpha(), // Èó≠ÂåÖÂàõÂª∫ÂΩìÂâçÂ≠óÊØç
      timer: "", //È°µÈù¢Êó∂Èó¥ÊòæÁ§∫ÁöÑÂÆöÊó∂Âô®
      showTime: 0, // È°µÈù¢ÊòæÁ§∫Êó∂Èó¥
      startTime: "", //ÂºÄÂßãÊó∂Èó¥
      records: [],
      rotate: false, // ÈáçÁΩÆÊåâÈíÆÂä®Áîª
    };
  },
  mounted() {
    // Á¶ÅÊ≠¢Á≤òË¥¥
    this.$refs.refInput.onpaste = function() {
      return false;
    };
  },
  computed: {
    upperCaseCurr() {
      return this.curr.toUpperCase();
    },
  },
  watch: {
    // ÁõëÂê¨inputÊõ¥Êñ∞ÁöÑÂÜÖÂÆπÔºåÂ¶ÇÊûú‰∏çÊòØÂ≠óÊØçÂ∞±ËàçÂºÉ
    inputVal(newV, oldV) {
      // console.log('changed');
      if (this.inputVal) {
        // ÂΩìÊúâÂÄºÁöÑÊó∂ÂÄôÂà§Êñ≠ÔºåÈÅøÂºÄ‰∏∫Á©∫ÁöÑÊÉÖÂÜµ
        const isAlphabet = /^[A-Za-z]+$/.test(newV);
        // console.log(isAlphabet);
        this.inputVal = isAlphabet ? newV : oldV;
      }
    },
  },
  methods: {
    initTimer() {
      this.timer = setInterval(() => {
        this.showTime = ((Date.now() - this.startTime) * 0.001).toFixed(3);
      }, 1);
    },
    handleKeyup(e) {
      // Âè™ÊúâËæìÂÖ•Â≠óÊØçÊâçÊúâÁõ∏Â∫îÔºåÂÖ∂‰ªñÊåâÈîÆÊó†Êïà
      // console.log(e);
      //e.keyCode > 64 && e.keyCode < 91 // ‰∏çÁî®keycode,Ëß£ÂÜ≥ios 9ÂÆ´Ê†ºÈîÆÁõòÈóÆÈ¢ò
      if (/^[A-Za-z]+$/.test(e.key.toLowerCase())) {
        // Âè™ÊúâËæìÂÖ•ÂΩìÂâçÊòæÁ§∫ÁöÑÂ≠óÊØçÊâçÁõ∏Â∫îÔºåÂÖ∂‰ªñÊó†Êïà
        if (e.key.toLowerCase() === this.curr) {
          const currTime = Date.now();
          // Â¶ÇÊûúÂΩìÂâçÊòØaÈÇ£‰πàÂ∞±ËÆæÁΩÆÂΩìÂâçÊó∂Èó¥‰∏∫ÂºÄÂßãÊó∂Èó¥, Âπ∂ÂêØÂêØÂä®ÂÆöÊó∂Âô®Êõ¥Êñ∞
          if (e.key.toLowerCase() === "a") {
            this.startTime = currTime;
            this.initTimer();
          }
          this.records.push({
            display: this.curr.toUpperCase(),
            currTime: ((currTime - this.startTime) / 1000).toFixed(3),
          });
          // Â¶ÇÊûúËæìÂÖ•Âà∞zËØ¥ÊòéËæìÂÖ•Âà∞ÊúÄÂêé‰∏Ä‰∏™Â≠óÊØçÔºåÁªìÊùüÂÆöÊó∂Âô®
          if (e.key.toLowerCase() === "z") {
            clearInterval(this.timer);
            // Áî±‰∫éÂÆöÊó∂Âô®Â≠òÂú®Êó∂Èó¥Â∑ÆÔºåÊúÄÂêé‰∏Ä‰∏™Êï∞ÂíåÊòæÁ§∫ÁöÑ‰∏çÁªü‰∏ÄÔºå
            // Âõ†Ê≠§Â∞ÜÊúÄÂêéÁöÑËÆ∞ÂΩïÁöÑÁªìÊûúËµãÂÄºÂà∞È°µÈù¢‰∏ä„ÄÇ
            this.showTime = this.records.slice(-1)[0].currTime;
          }
          //Áî®Èó≠ÂåÖÁîüÊàê‰∏ã‰∏Ä‰∏™Â≠óÊØçÔºåÂπ∂ÊääÂÆÉËµãÂÄºÂà∞ÂΩìÂâç
          this.curr = this.alpha();
        } else {
          e.preventDefault();
        }
      } else {
        e.preventDefault();
      }
    },
    handleReset() {
      this.inputVal = ""; // ÁΩÆÁ©∫ input
      this.curr = "a"; // ÈáçËÆæÁΩÆÂΩìÂâçÂ≠óÊØç‰∏∫ÂàùÂßãÂÄºa
      this.alpha = null; // ÁΩÆÁ©∫Âπ∂ÈáçÊñ∞ÂàõÂª∫Èó≠ÂåÖ
      this.alpha = this.createAlpha();
      this.startTime = ""; // ÁΩÆÁ©∫ÂºÄÂßãÊó∂Èó¥ÂíåËÆ∞ÂΩï
      this.records = [];
      clearInterval(this.timer);
      this.showTime = 0;
      // this.$refs.refInput.focus();
    },
    createAlpha() {
      let curr = 0;
      let list = [
        "a",
        "b",
        "c",
        "d",
        "e",
        "f",
        "g",
        "h",
        "i",
        "j",
        "k",
        "l",
        "m",
        "n",
        "o",
        "p",
        "q",
        "r",
        "s",
        "t",
        "u",
        "v",
        "w",
        "x",
        "y",
        "z",
      ];
      return () => {
        curr++;
        return curr < list.length ? list[curr] : "ÂÆåÊàê";
      };
    },
    handleRotate: _.throttle(function () {
        this.rotate = true;
        setTimeout(() => {
          this.rotate = false;
        }, 1000);
    }, 1000)
  },
};
</script>

<style lang="scss">
@import "./style.scss";
</style>
